@keyframes pulsate {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.1);
    opacity: 0.7;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

.pulsate-effect {
  animation: pulsate 1.5s infinite;
}

.placeholderText {
  color: #5C5C5C;
  font-style: bold;
}


.englishText {
  color: #5C5C5C;
  font-style: italic;
}

.promptText {
  color: black;
}


.greenColor {
  transition: color 0.3s;
  color: #00E540;
}

.redColor {
  transition: color 0.3s;
  color: #FF002E;
}

