.quick-tip {
  display: flex;
  position: relative;
}

.quick-tip .main {
  align-items: flex-end;
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  gap: 16px;
  padding: 24px 32px;
  position: relative;
}

.quick-tip .text {
  align-items: flex-start;
  align-self: stretch;
  display: flex;
  flex: 0 0 auto;
  flex-direction: column;
  gap: 8px;
  position: relative;
  width: 100%;
}

.quick-tip .new-feature {
  align-self: stretch;
  font-family: "Roboto", Helvetica;
  font-size: 18px;
  font-weight: 700;
  letter-spacing: 0;
  line-height: normal;
  margin-top: -1px;
  position: relative;
}

